<template>
    <div class="author-main" v-if="data">
    <div class="user" v-if="data.id">
        <div class="informations">
        <img :src="data.image" :alt="data.name">
        <span class="name">{{ data.name }}</span>
        <span v-if="data.isMyFriends">我的好友</span>
        <span>
            <el-button class="send-friends-app"
            v-if="!data.hasApp && !data.isMyFriends && !data.IamAuthor"
            @click="send_friends_app(data.id)"
            :loading="isSendding">
            添加好友
            </el-button>
        </span>
        <p class="last-time">
            上次登陆时间{{ change_date(data.last_time) }}
        </p>
        </div>
    </div>
    <div v-if="data.posts" class="author-posts">
        <div class="posts-len">他的留言板 {{ data.posts.length }}</div>
        <div class="posts">
        <div class="post" v-for="(post, index) in data.posts" :key="index">
            <p class="title">
            <router-link style="text-decoration: none;" :to="`/Post/${post.id}`">
                {{ post.title }}
            </router-link>
            </p>
            <pre class="summary">{{ post.summary }}</pre>
            <p class="create_time">
            {{ change_date(post.create_time) }}
            </p>
            <hr>
        </div>
        </div>
    </div>
    </div>
</template>
<script>
import {ref,onMounted} from'vue';
import {get} from '@/http/httpRequest';
import { notice } from '@/components/notice';
import { useRoute } from 'vue-router';
import { useUrl } from '@/store/useUrl';

export default{
    setup(){
        const route=useRoute();
        const urls=useUrl();
        const data=ref({});
        const userId=route.params.userId;
        const isSendding=ref(false);

        const init=async()=>{
            const url=urls.athor+'/'+userId;
            const response=await get(url);
            if(response)
            data.value=response.data;
        }

        const send_friends_app=async (id)=>{
            const url=urls.send_friend_app+"/"+id;
            isSendding.value=true;
            const res=await get(url);
            if(res){
                if(!data.value.hasApp)
                data.value.hasApp=true;
                notice("已申请好友","",2000);
            }
            isSendding.value=false;
        }

        onMounted(async()=>{
            await init();
        });

        const change_date=(str)=>{
            try{
                const date=new Date(str);
                const month=date.getMonth()+1;
                const days=date.getDay();
                const hour=date.getHours();
                return month+'-'+days+'-'+hour+":00";

            }catch(e){
                return str;
            }
        }
        return{
            data,
            send_friends_app,
            isSendding,
            change_date
        }  
    }
}

</script>
